<template>
    <section>
        <div class="">
            <div>
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"></span>
                    <span class="p-actions float-md-right float-left mb-wid-100">
                        <a href="javascript:;" class="p-action-btn text-white d-md-inline-block mble_50"
                            v-on:click="ShowHideMap">
                            <em id="icnToggleMap" class="fa fa-angle-double-up"></em>
                            <br>
                            {{ mapHeaderText }}</a>
                        <RouterLink to="/Ticketing/WorkOrderListing" title=""
                            class="p-action-btn text-white d-md-inline-block mble_50" data-toggle-tooltip="tooltip"
                            data-original-title="Back to list">
                            <i class="fa fa-angle-double-left"></i>
                            <br>Back to list
                        </RouterLink>
                    </span>
                </div>
            </div>
        </div>
        <MapComponentVue v-if="isShowMap == true" class="row" mapContainer="WorkOrderDetail" mapType="MapBox" :Data="mapObj"
            :config="config"></MapComponentVue>
        <div class="row">
            <div class="container-fluid padding-t_10">
                <div class="left-menu-tab">
                    <div class="col-md-12 p-0 ">
                        <div class="theme-primary partition">
                            <span class="p-name text-white textelipsis"><a title="WorktypeName"
                                    data-toggle-tooltip="tooltip"><span></span>{{ WorkOrderNumber }}</a></span>
                        </div>
                    </div>
                    <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab fsmtabcontrol lefttabiconfix"
                        id="myTab" role="tablist">
                        <li class="nav-item ">
                            <a class="nav-link active left-menu" data-toggle="tab" href="#Details" @click="Details"
                                id="tab-details" role="tab" aria-controls="DetailsText" aria-selected="true"
                                title="Details"><em class="fa fa-file-text" aria-hidden="true"></em>Details</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" id="tab-resapproval" href="#ServiceAppointment"
                                @click="ServiceAppointment" role="tab" aria-controls="ServiceAppointment"
                                aria-selected="false" title="@Resources.ServiceAppointment"><em class="fa fa-calendar"
                                    aria-hidden="true"></em>Service Appointment</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" id="tab-timetrackhistory"
                                @click="TimeTrackHistory" href="#TimeTrackHistory" role="tab"
                                aria-controls="TimeTrackHistory" aria-selected="false"
                                title="@Resources.Time @Resources.Track @Resources.History"><em class="fa fa-clock-o"
                                    aria-hidden="true"></em>Time Track History</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" @click="GetWorkOrderStatusHistory"
                                id="tab-viewstatushistory" href="#ViewStatusHistory" role="tab"
                                aria-controls="ViewStatusHistory" aria-selected="false"
                                title="@Resources.View @Resources.Status @Resources.History"><em class="fa fa-clock-o"
                                    aria-hidden="true"></em>View Status History</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" @click="GetReportIncidentListing"
                                id="tab-reportincident" href="#ReportIncident" role="tab" aria-controls="ReportIncident"
                                aria-selected="false" title="@Resources.Report @Resources.Incident"><em class="fa fa-file"
                                    aria-hidden="true"></em>Report Incident</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" @click="FetchAttachmentDatas"
                                id="tab-attachments" href="#Attachments" role="tab" aria-controls="Attachments"
                                aria-selected="false" title="@Resources.Attachments"><em class="fa fa-file"
                                    aria-hidden="true"></em>Attachments</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" @click="LoadNoteForQuickView" id="tab-notes"
                                role="tab" aria-controls="Notes" aria-selected="false" title="@Resources.Notes"><em
                                    class="fa fa-file-text" aria-hidden="true"></em>Notes</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" href="#tab-requiredproduct"
                                @click="RequiredProduct" id="tab-requiredproduct" role="tab"
                                aria-controls="RequiredProducts" aria-selected="false"
                                title="@Resources.RequiredProducts"><em class="fa fa-file-text"
                                    aria-hidden="true"></em>Required Products</a>
                        </li>
                    </ul>
                </div>
                <div class="right-content">
                    <div class="col-md-12 p-0 mb-2">
                        <div  :class="{ 'p-actions-collapse': isDisabledCollapse, 'theme-primary partition p-actions-expand': true }">
                            <span class="p-name text-white"><a data-toggle-tooltip="tooltip">{{ headerText }}</a></span>
                            <span class="p-actions float-right">
                                <a href="javascript:;" class="p-action-btn text-white" v-on:click="AddNote(null)"
                                    title="AddNew Notes"><i class="fa fa-plus" aria-hidden="true"></i><br>Add New Notes</a>
                                <a href="javascript:;" class="p-action-btn text-white" v-on:click="openAttachmentPopup()"
                                    title="Add Attachment"><i class="fa fa-plus" aria-hidden="true"></i><br>Add
                                    Attachment</a>
                                <a href="javascript:;" class="p-action-btn text-white"
                                    v-on:click="openReportIncidentPopUp()" title="Add Incident"><i class="fa fa-plus"
                                        aria-hidden="true"></i><br>Add Incident</a>
                                <a href="javascript:;" class="p-action-btn text-white" @click="StartTimer"
                                    v-if="AddEditText != 'Edit'" title="StartTimer" id="StartTimer"><i
                                        class="fa fa-clock-o pr-2" aria-hidden="true"></i><br>Start Timer</a>
                                <a href="javascript:;" class="p-action-btn text-white" title="StopTimer"
                                    v-if="AddEditText === 'Edit'" @click="openStopPopUp" id="StopTimer"><i
                                        class="fa fa-clock-o pr-2" aria-hidden="true"></i><br>Stop Timer</a>
                                <RouterLink to="/FSM/workorderlisting" title="Back"
                                    class="p-action-btn text-white d-md-inline-block mble_50" data-toggle-tooltip="tooltip"
                                    data-original-title="Back to list"><i class="fa fa-angle-double-left"
                                        aria-hidden="true"></i><br>Back to list</RouterLink>
                                <a href="javascript:;" class="p-action-btn text-white disabled btnDelete d-none"
                                    v-on:click="DeleteIncidentList()" id="DeleteMultiple"><i
                                        class="fa fa-trash pr-2"></i><br>Delete</a>
                                <a title="Schedule"
                                    v-if="Status_Id != 'Completed' && Status_Id != 'Failed' && Status_Id != 'Canceled' && SA_Id != null"
                                    href="javascript:;" class="p-action-btn text-white">
                                    <i class="fa fa-clock-o action-icon" aria-hidden="true"></i><br>Schedule
                                </a>

                            </span>
                            <span class="collapse-head-btns" @click="toggleCollapse()">
                                    <a href="javascript:;" class="collapse-btns"><em
                                            class="fa fa-angle-up text-white"></em></a>
                                </span>
                        </div>
                    </div>
                    <WorkOrderDetailTab v-if="WorkOrderDetailTab" :workOrderDetailData="workOrderDetailData"
                        :WO_Status_Id="WO_Status_Id" :isLocateorNonLocate="isLocateorNonLocate" :workOrderId="workOrderId"
                        :ticketId="ticketId" v-bind:callbackfunction="FetchRecords"></WorkOrderDetailTab>
                    <AddEditNote v-if="isShowAddNote" @Close="Close" :configdata="configData" :id="ticketId"
                        @LoadNoteForQuickView="LoadNoteForQuickView"></AddEditNote>
                    <ShowAttachmentDataVue v-if="showAttachmentModal" ListViewAttachment="1" :id="ticketId"
                        :configdata="configData" @FetchAttachmentData="FetchAttachmentDatas"></ShowAttachmentDataVue>
                    <AddIncidentVue v-if="showIncidentPopup" @Close="Close" :ticketId="ticketId" :workOrderId="workOrderId">
                    </AddIncidentVue>
                    <ServiceAppointmentByIdVue :workOrderId="workOrderId" :totalRecord="totalRecord"
                        v-if="showServiceAppointment"></ServiceAppointmentByIdVue>
                    <TimeTrackHistoryVue v-if="showTimeTrackHistory" :workOrderId="workOrderId" :key="refreshTimeTrack"
                        :ticketId="ticketId" :totalRecord="totalRecord" @TimeTrackHistory="TimeTrackHistory"
                        :TimeTrackHistoryData="TimeTrackHistoryData"></TimeTrackHistoryVue>
                    <TicketAttachmentVue v-if="showTicketAttachment" ListViewAttachment="1" :key="componentKey"
                        @open-image="openSpecificImage" @GetAttachmentData="GetAttachmentData"
                        :AttachmentData="AttachmentData" viewMode="AttachmentInWorkOrderDetail"></TicketAttachmentVue>
                    <TicketNotesVue v-if="showNotes" :NoteData="NoteList"></TicketNotesVue>
                    <ReportIncidentListingVue v-if="showReportListing" :totalRecord="totalRecord" :workOrderId="workOrderId"
                        :ticketId="ticketId" :ReportIncidentListingData="ReportIncidentListingData" :key="refreshKey">
                    </ReportIncidentListingVue>
                    <WorkOrderStatusHistory v-if="showWorkOrderStatusHistory" :workOrderId="workOrderId"
                        :ticketId="ticketId" :totalRecord="totalRecord" :WorkOrderStatusHistory="WorkOrderStatusHistory">
                    </WorkOrderStatusHistory>
                    <WorkOrderTimerModalVue v-if="ShowTimerMoadal" :id="ticketId" @Close="Close"></WorkOrderTimerModalVue>
                    <VueEasyLightbox ref="lightbox" :show="true" v-if="visible" v-bind:images="galleryImages"
                        @close="visible = false" :customClasses="lightboxClasses"></VueEasyLightbox>
                    <RequiredProductListVue :workOrderId="workOrderId" v-if="showRequiredProduct"></RequiredProductListVue>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import MapComponentVue from "../../components/Common/MapComponent.vue";
import DataService from '../../services/DataService';
import WorkOrderDetailTab from "./WorkOrderDetailTab.vue";
import AddEditNote from "../Ticketing/AddNoteData.vue";
import ShowAttachmentDataVue from '../Ticketing/ShowAttachmentData.vue';
import AddIncidentVue from './AddIncident.vue';
import ServiceAppointmentByIdVue from './ServiceAppointmentById.vue';
import TimeTrackHistoryVue from './TimeTrackHistory.vue';
import TicketAttachmentVue from '../../components/Modal/TicketAttachment.vue';
import TicketNotesVue from '../../components/Modal/TicketNotes.vue';
import ReportIncidentListingVue from './ReportIncidentListing.vue';
import WorkOrderStatusHistory from "./WorkOrderStatusHistory.vue";
import WorkOrderTimerModalVue from '../../components/Modal/WorkOrderTimerModal.vue';
import VueEasyLightbox from 'vue-image-lightbox-carousel';
import RequiredProductListVue from './RequiredProductList.vue';
export default {
    data() {
        return {
            config: {
                isShowDrawButton: false,
                isShowEtaMap: true,
                isShowLocator: false,
                isShowEtaPipeLines: false,
                isShowGetDirection: true
            },
            WorkOrderNumber: 'WO-',
            refreshKey: 0,
            showWorkOrderStatusHistory: false,
            mapObj: [],
            showTicketAttachment: false,
            isShowMap: false,
            workOrderId: 0,
            ticketId: 0,
            refreshTimeTrack: 0,
            workOrderDetailData: [],
            mapHeaderText: 'Collapse map',
            isShowAddNote: false,
            showAttachmentModal: false,
            showIncidentPopup: false,
            showServiceAppointment: false,
            showRequiredProduct: false,
            WorkOrderDetailTab: false,
            headerText: 'Detail',
            totalRecord: 0,
            pageNumber: 1,
            pageSize: 10,
            sortBy: "",
            sortExp: "",
            TimeTrackHistoryData: [],
            showTimeTrackHistory: false,
            AttachmentData: [],
            galleryImages: [],
            NoteList: [],
            showNotes: false,
            showReportListing: false,
            pageNumber: 1,
            pageSize: 10,
            sortBy: '',
            sortExp: '',
            ReportIncidentListingData: [],
            WorkOrderStatusHistory: [],
            ShowTimerMoadal: false,
            componentKey: 0,
            visible: false,
            isLocateorNonLocate: '',
            WO_Status_Id: 0,
            configData: []
        }
    },
    components: {
        MapComponentVue,
        WorkOrderDetailTab,
        AddEditNote,
        ShowAttachmentDataVue,
        AddIncidentVue,
        ServiceAppointmentByIdVue,
        TimeTrackHistoryVue,
        TicketAttachmentVue,
        TicketNotesVue,
        ReportIncidentListingVue,
        WorkOrderStatusHistory,
        WorkOrderTimerModalVue,
        VueEasyLightbox,
        RequiredProductListVue
    },
    created: function () {
        ////debugger;
        var vm = this;
        vm.workOrderId = vm.$route.params.id;
        vm.ticketId = vm.$route.params.ticket_id;
        vm.FetchRecords();
    },
    methods: {
        handleHide: function () {
            this.visible = false
        },
        openSpecificImage(attachmentPath, attachmentFileName) {
            var vm = this;
            var obj = {
                path: attachmentPath,
                //caption: "Attachment " + (index1+1),
                caption: attachmentFileName.split('.')[0]
            }
            vm.galleryImages.push(obj);
        },
        StartTimer: function () {
            var vm = this;
            //vm.AddEditText = "Edit";
            vm.ShowTimerMoadal = true;
        },
        Details: function () {
            var vm = this;
            vm.headerText = "Detail"
            vm.showServiceAppointment = false;
            vm.WorkOrderDetailTab = true;
            vm.showTimeTrackHistory = false;
            vm.showWorkOrderStatusHistory = false;
            vm.showRequiredProduct = false;
            vm.showTicketAttachment = false;
            vm.showNotes = false;
            vm.showReportListing = false;
        },
        ServiceAppointment: function () {
            var vm = this;
            vm.headerText = "Service Appointment"
            vm.showServiceAppointment = true;
            vm.showWorkOrderStatusHistory = false;
            vm.showTimeTrackHistory = false;
            vm.WorkOrderDetailTab = false;
            vm.showTicketAttachment = false;
            vm.showNotes = false;
            vm.showRequiredProduct = false;
            vm.showReportListing = false;
        },
        urlChange: function () {
            const query = Object.assign({}, this.$route.query); query.reload = 'reload'
            this.$router.push({ query: query })
        },
        openStopPopUp: function () {
            var vm = this;
            var jsonobj =
            {
                TicketId: vm.ticketId,
                TrackId: 0,
                RequestFor: 'FSM',
                RequestForType: 'STOP',
                IPAddress: ''

            };
            var params = JSON.stringify(jsonobj);

            vm.confirmR(vm.$t('AreYouSureToStopTimer'), true, vm.$t('StopTimer'), function (result) {
                vm.Disabled = 1;
                DataService.StopTrackHistory(params).then((response) => {
                    try {
                        if (response.data.status == 'Success') {
                            vm.$parent.ShowTimer = false;
                            vm.AddEditText = "Add";
                            vm.Disabled = 0;
                            vm.urlChange();
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("TimeTrack")]), "success", true, vm.$t("Alert"));
                            vm.refreshTimeTrack += 1;
                            // vm.FetchData();
                            document.getElementById('tab-trackhistory').click();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("TimeTrack")]), "failure", true, vm.$t("Alert"));
                            vm.Disabled = 0;
                            vm.Close();
                        }
                    }
                    catch (exinner) {
                        return HandleErrorOccured(exinner, 'failure');
                    }
                });
            });
        },
        openReportIncidentPopUp: function () {
            var vm = this;
            vm.showIncidentPopup = true;
        },
        openAttachmentPopup: function () {
            var vm = this;
            vm.showAttachmentModal = true;
        },
        AddNote: function () {
            debugger;
            var vm = this;
            vm.isShowAddNote = true;
        },
        Close: function () {
            var vm = this;
            vm.ShowTimerMoadal = false;
            vm.isShowAddNote = false;
            vm.showIncidentPopup = false;
        },
        GetWorkOrderStatusHistory: function () {
            var vm = this;
            vm.headerText = "View Status History"
            vm.showWorkOrderStatusHistory = true;
            vm.showServiceAppointment = false;
            vm.showTimeTrackHistory = false;
            vm.WorkOrderDetailTab = false;
            vm.showRequiredProduct = false;
            vm.showTicketAttachment = false;
            vm.showNotes = false;
            vm.showReportListing = false;
        },
        FetchAttachmentDatas: function () {
            var vueObj = this;
            vueObj.headerText = "Attachments"
            vueObj.showWorkOrderStatusHistory = false;
            vueObj.showTicketAttachment = true;
            vueObj.showTimeTrackHistory = false;
            vueObj.showReportListing = false;
            vueObj.showServiceAppointment = false;
            vueObj.showRequiredProduct = false;
            vueObj.WorkOrderDetailTab = false;
            vueObj.showNotes = false;
            vueObj.AttachmentData = [];
            var url = `ticketId=${vueObj.ticketId}`;
            DataService.GetTicketFiles(url).then(function (response) {
                vueObj.isLoading = false;
                vueObj.AttachmentData = response.data.result.DATA;
                vueObj.componentKey += 1
            });
        },
        LoadNoteForQuickView: async function (moduleName) {
            var vm = this;
            vm.headerText = "Notes"
            vm.showWorkOrderStatusHistory = false;
            vm.showTicketAttachment = false;
            vm.showTimeTrackHistory = false;
            vm.showServiceAppointment = false;
            vm.showRequiredProduct = false;
            vm.showReportListing = false;
            vm.WorkOrderDetailTab = false;
            vm.showNotes = true;
            var url = `ticketId=${vm.ticketId}&isSuptTckt=false&moduleName=Ticketing&isView=1`;
            await DataService.GetWorkOrderNoteList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                }
                else {
                    vm.noRecord = true;
                }
            });
        },
        GetReportIncidentListing: function () {
            var vm = this;
            vm.headerText = "Report Incident";
            vm.showWorkOrderStatusHistory = false;
            vm.showReportListing = true;
            vm.showTicketAttachment = false;
            vm.showRequiredProduct = false;
            vm.showTimeTrackHistory = false;
            vm.showServiceAppointment = false;
            vm.WorkOrderDetailTab = false;
            vm.showNotes = false;
            var params = `id=${vm.workOrderId}&ticketId=${vm.ticketId}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.pageSize}&pageNumber=${vm.pageNumber}`
            DataService.FetchReportIncidentRecords(params).then(function (response) {
                vm.ReportIncidentListingData = response.data.result.data;
                vm.totalRecord = response.data.result.data[0].TOTALRECORDS;
                vm.refreshKey += 1;
            });
        },
        ShowHideMap: function () {
            var icon = $('#icnToggleMap');
            if (this.isShowMap == false) {
                this.isShowMap = true;
                this.mapHeaderText = 'Collapse map'
                if (icon.hasClass('fa-angle-double-down')) {
                    icon.removeClass('fa-angle-double-down');
                    icon.addClass('fa-angle-double-up');
                }
            }
            else {
                this.isShowMap = false;
                this.mapHeaderText = 'Expand map'
                if (icon.hasClass('fa-angle-double-up')) {
                    icon.removeClass('fa-angle-double-up');
                    icon.addClass('fa-angle-double-down');
                }
            }
        },
        RequiredProduct: function () {
            ////debugger;
            var vm = this;
            vm.headerText = 'Required Product';
            vm.showServiceAppointment = false;
            vm.showWorkOrderStatusHistory = false;
            vm.showTimeTrackHistory = false;
            vm.WorkOrderDetailTab = false;
            vm.showTicketAttachment = false;
            vm.showNotes = false;
            vm.showReportListing = false;
            vm.showRequiredProduct = true;
        },
        TimeTrackHistory: function () {
            var vm = this;
            vm.headerText = "Time Track History"
            vm.showTimeTrackHistory = true;
            vm.showReportListing = false;
            vm.showTicketAttachment = false;
            vm.showWorkOrderStatusHistory = false;
            vm.showServiceAppointment = false;
            vm.WorkOrderDetailTab = false;
            vm.showRequiredProduct = false;
        },
        FetchRecords: async function () {
            debugger
            var vm = this;
            vm.mapObj = [];
            var params = `moduleName=TICKETING&subModuleCode=FSM_WORKORDER&id=${vm.workOrderId}`;
            await DataService.ViewPageDetails(params).then(function (response) {
                var locationlatlong;
                var result = response.data.result.LEFTDATA;
                vm.configData = response.data.result.CONFIG_DATA;

                result.forEach(function (row) {
                    if (row.name == "LOCATION_NW") {
                        locationlatlong = row.value;
                    }
                    else if (row.name == "workorder_number") {
                        vm.WorkOrderNumber = row.value;
                    }
                })
                if (response.data.result.IN_COMPLETE_ENTRY == '1') {
                    vm.AddEditText = "Edit";
                }
                vm.workOrderDetailData = response.data.result.LEFTDATA;
                vm.WorkOrderDetailTab = true;
                vm.workOrderDetailData.forEach(function (row) {
                    if (row.name == "status_id") {
                        vm.WO_Status_Id = vm.FilterDDLValueForID(row.value);
                    }
                });
                vm.isLocateorNonLocate = response.data.result.IS_FSM_LOCATING;
                var obj = {
                    latlong: locationlatlong,
                    refValue: "62887053677063",
                    popupHeaderHtml: '',
                    popupHeaderColor: "",
                    popupBodyHtml: '',
                    popupFooterHtml: '',
                    pinLatLong: {
                        latitude: locationlatlong,
                        longitude: locationlatlong
                    },
                    polygons: [
                        {
                            name: "",
                            latLong: [{
                                latitude: locationlatlong,
                                longitude: locationlatlong,
                            }
                            ]
                        }
                    ],
                    address: "",
                    priority: '',
                    color_code: ''
                }
                vm.mapObj.push(obj);
                vm.isShowMap = true;
            })
        }
    }
}
</script>

